import "../../styles/loan-common.css";

const EducationLoan = () => {
  return (
    <section
      className="loan-hero"
      style={{ backgroundImage: "url('/assets/loanEducation.jpg')" }}
    >
      <div className="loan-overlay"></div>

      <div className="loan-container">
        {/* LEFT CONTENT */}
        <div className="loan-left">
          <h1>Education Loan</h1>
          <ul>
            <li>✅ Lowest rates starting at 9.25% p.a.</li>
            <li>✅ Quick loan sanction for higher studies</li>
            <li>✅ Part-prepayment option available</li>
            <li>✅ Loan amount up to ₹1 Crore</li>
            <li>✅ T&amp;C Apply</li>
          </ul>
          <button className="loan-btn">
            Fulfill Your Education Dreams - Apply Now
          </button>
        </div>

        {/* RIGHT FORM */}
        <div className="loan-right">
          <h2>Looking for a Education Loan?</h2>
          <p>Let us help you find the most suitable offers.</p>

          <input type="text" placeholder="Name as Per Pancard" />
          <input type="tel" placeholder="Your Mobile Number" />

          <button className="loan-btn loan-full">Apply</button>
        </div>
      </div>
    </section>
  );
};

export default EducationLoan;
